    auxiComp = {
        compIndex: 999,
        name:'',
        type:'auxi',
        data: {
            title: ''
        },
        style: this._defCompStyle()
    }

    areaComp = {
        compIndex: 999,
        name:'',
        type:'area',
        data: {
            title: ''
        },
        style: this._defCompStyle()
    }

    _defCompStyle(customStyle ?:any,appendStyle?:any) {
        let styleJson = {
            position: 'absolute', //|position
            top:400, //|postion
            left:200,
            height:100, //|size
            width: 100,
            borderColor: "rgba(255,0,0,0.6)", //|border
            borderWidth: 1,
            borderStyle: 'solid',
            paddingTopBottom:0, //|padding
            paddingLeftRight:0,
            fontSize:14,
            textAlign:'center', //|textAlign
            userDrag:'element'
   
        }
        for(let key in customStyle) {
            styleJson[key] = customStyle[key];
        }
        for(let key in appendStyle) {
            styleJson[key] = appendStyle[key];
        }
        return styleJson;
    }

    getAuxiComp() {
        return this.auxiComp;
    }

    getAreaComp() {
        return this.areaComp;
    }

