import { Component, OnInit } from '@angular/core';
import { EmitSubService } from 'src/app/core/emitSub/emit-sub.service';
import { CustomBasicComponent } from '../../../comp-basic/custom-basic/custom-basic.component';

@Component({
  selector: 'app-chart-pie',
  templateUrl: './chart-pie.component.html',
  styleUrls: ['./chart-pie.component.scss']
})
export class ChartPieComponent extends CustomBasicComponent implements OnInit {

  chartOption:any;
  constructor(private emitService: EmitSubService) {
    super(emitService)
   }

  ngOnInit(): void {
    this.initBasicData();
    this.initData();
  }

  initData() {
    this.chartOption = {

      legend: {
          orient: 'vertical',
          left: 'left',
          data: ['','','']
      },
      series : [
          {
              name: '',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                  {value:335, name:''},
                  {value:310, name:''},
                  {value:234, name:''},
              ],
              itemStyle: {
                  emphasis: {
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
              }
          }
      ]
    };
  }  
}
